.Tile {
    width: 100px;
    height: 100px;
    margin: 10px;
    background-color: lightgrey;
    display: inline-block;
    cursor: pointer;
    opacity: .5;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    font-size: 4em;
    line-height: 100px;
    text-align: center;
    font-weight: bold;
}

.Tile_opened {
    opacity: 1;
}



